<template>
    <div class="header">
        <div class="wrap">
            <div class="logo"></div>
            <div class="menu">
                <div class="menu_div" :class="{menu_active: menuNum==1}" @click="menuNum=1">发现音乐</div>
                <div class="menu_div" :class="{menu_active: menuNum==2}" @click="menuNum=2">我的音乐</div>
                <div class="menu_div" :class="{menu_active: menuNum==3}" @click="menuNum=3">朋友</div>
                <div class="menu_div" :class="{menu_active: menuNum==4}" @click="menuNum=4">商城</div>
                <div class="menu_div" :class="{menu_active: menuNum==5}" @click="menuNum=5">音乐人</div>
                <div class="menu_div lastmenu" :class="{menu_active: menuNum==6}" @click="menuNum=6">下载客户端</div>
            </div>
            <div class="search">
                <input type="text" class="s_input" placeholder="音乐/视频/电台/用户">
            </div>
            <div class="author">
                创作者中心
            </div>
            <div class="info" @mouseenter="showInfo()" @mouseleave="hideInfo()">
                <img src="../../../static/images/discover/tx.jpg" alt="" class="info_img">
                <div class="mainInfo" v-show="infoFlag">
                    <ul>
                        <li><i class="imgi img1"></i>我的主页</li>
                        <li><i class="imgi img2"></i>我的消息</li>
                        <li><i class="imgi img3"></i>我的等级</li>
                        <li><i class="imgi img4"></i>VIP会员</li>
                        <li><i class="imgi img5"></i>个人设置</li>
                        <li><i class="imgi img6"></i>实名认证</li>
                        <li><i class="imgi img7"></i>退出</li>
                    </ul>
                    <i class="arr"></i>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'pageHeader',
    data() {
        return {
            menuNum: 1,
            infoFlag: false
        }
    },
    methods: {
        showInfo() {
            this.infoFlag = true;
        },
        hideInfo() {
            this.infoFlag = false;
        }
    }
}
</script>

<style lang="less" scoped>
@import './pageHeader.less';
</style>



